:root {
    /***********Colors*****/
    --first-color: rgb(209, 209, 177);
    --title-color: hsl(0, 0%, 40%);
    --text-color: hsl(0, 0%, 50%);
    --body-color: hsl(0, 0%, 100%);
    --container-color: hsl(0, 0%, 93%);
    --border-color: hsl(0, 0%, 87%);
    --text-white: #fff;
    /**/
    --body-font: "Open Sans", sans-serif;
    --second-font: 'Poppins', sans-serif;
    --big-font-size: 56px;
    --h1-font-size: 50px;
    --h2-font-size: 32px;
    --h3-font-size: 24px;
    --large-font-size: 18px;
    --normal-font-size: 16px;
    --small-font-size: 15px;
    --smaller-font-size: 14px;
    --tiny-font-size: 12px;
    /**/
    --weight-400: 400;
    --weight-500: 500;
    --weight-600: 600;
    --weight-700: 700;
    --weight-800: 800;

}
.dark-theme{
    --title-color: hsl(0, 0%, 100%);
    --text-color: hsl(0, 0%, 80%);
    --body-color: hsl(0, 0%, 7%);
    --container-color: hsl(0, 1%, 17%);
    --border-color: hsl(0, 0%, 15%);
}
.style__switcher{
    position: fixed;
    left: 0;
    top: 10%;
    background-color: var(--text-white);
    z-index: 10;
    width: 220px;
    padding: 15px;
    box-shadow: 0 0 4px hsl(0,0%, 80%);
    transform: translateX(-100%);
    transition: all 0.3s ease;

}
.show-switcher{
    transform: translateX(0);
}
.style__switcher,
.style__switcher-toggler,
.theme__toggler{
    border-radius: 0 5px 5px 0;
}
.style__switcher-items{
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    column-gap: 4px;
    row-gap: 8px;
}
.style__switcher-title,
.style__switcher-toggler,
.theme__toggler,
.style__switcher-close{
    color: hsl(0, 0%, 40%);
}
.style__switcher-title{
    font-size: var(--small-font-size);
    font-weight: var(--weight-600);
    border-bottom: 1px solid hsl(0, 0%, 87%);
    padding-bottom: 5px;
    margin-bottom: 15px;
}
.theme__img{
    width: 30px;
    cursor: pointer;
}
.style__switcher-toggler,
.theme__toggler{
    position: absolute;
    left: 100%;
    background-color: var(--text-white);
    height: 50px;
    width: 50px;
    font-size: var(--large-font-size);
    cursor: pointer;
    display: grid;
    place-items: center;
    box-shadow: 0 0 2px hsl(0, 0%, 80%);
    transition: all 0.3s ease;
}
.show-switcher .style__switcher-toggler,
.show-switcher .theme__toggler{
    left: -100%;
}
.style__switcher-toggler{
    top: 10%;
}
.theme__toggler{
    bottom: 10%;
}
.style__switcher-close{
    cursor: pointer;
    font-size: var(--h2-font-size);
    position: absolute;
    top: 2%;
    right: 15px;
}

